
* {
  margin: 0;
  padding: 0;
}
ul,
li {
  list-style-type: none;
  margin: 0;
}
a {
  text-decoration: none;
}
input:focus::-webkit-input-placeholder {
  color: transparent;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  position: relative;
  height: 100vh;
}
#app .search {
  position: absolute;
  right: 0;
  top: 10px;
  margin-right: 10px;
}
#app .search .search-input {
  -webkit-appearance: none;
  outline: none;
  width: 100px;
  border-radius: 10px;
  border: 1px solid #f9cb32;
  padding: 2px 22px 2px 10px;
  font-size: 14px;
  line-height: 14px;
}
#app .search .search-btn {
  position: absolute;
  right: 5px;
  top: 5px;
  width: 13px;
  height: 13px;
  background-image: url("./assets/search-btn.png");
}
#app .tab {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 40px;
  line-height: 40px;
  border-radius: 5px 5px 0 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background: #8adad0;
}
#app .tab .tab-item {
  width: 50%;
  text-align: center;
}
#app .tab .tab-item > a {
  display: block;
  text-decoration: none;
  font-size: 14px;
  color: #4d555d;
  font-size: 20px;
}
#app .tab .tab-item > a.router-link-active {
  color: #f01414;
}
#app .content {
  max-height: calc(100vh - 80px);
}


