
.movies {
  background-color: #ccc;
  position: absolute;
  left: 0;
  top: 40px;
}
.movies .title {
  width: 100%;
  text-align: center;
  font-size: 14px;
  height: 30px;
  line-height: 30px;
}
.movies .title > span {
  font-size: 14px;
  font-weight: bold;
  color: #6b5e5e;
}
.movies .movie-list {
  height: calc(100vh - 110px);
  border-radius: 10px;
  border: 1px solid #ccc;
  background: #fff;
  overflow: hidden;
}
.movies .movie-list .movie-box {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  margin: 10px 0;
}
.movies .movie-list .movie-box .movie-item {
  width: 30%;
  margin-left: 2.5%;
  margin-bottom: 20px;
}
.movies .movie-list .movie-box .movie-item .show-box {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column nowrap;
          flex-flow: column nowrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.movies .movie-list .movie-box .movie-item .show-box .img-show {
  border: 1px solid #ccc;
  border-radius: 10px;
  height: 150px;
}
.movies .movie-list .movie-box .movie-item .show-box .img-show > img {
  width: 100%;
  height: 100%;
}
.movies .movie-list .movie-box .movie-item .show-box .movie-title {
  font-size: 13px;
  font-weight: 400;
  padding: 10px 0 0;
  color: #494949;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}


